  <Line :data="chartData" :options="chartOptions" />
</template>

<script>
// eslint-disable-next-line import/no-extraneous-dependencies
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
} from 'chart.js';
// eslint-disable-next-line import/no-extraneous-dependencies
import { Line } from 'vue-chartjs';

ChartJS.register(CategoryScale, LinearScale, PointElement, LineElement, Title, Tooltip, Legend);

export default {
  name: 'App',
  components: {
    Line,
  },
  data() {
    return {
      chartData: {
        labels: ['Q1', 'Q2', 'Q3', 'Q4'],
        datasets: [
          {
            label: '2021',
            backgroundColor: '#f87979',
            data: [40, 39, 10, 40],
          },
          {
            label: '2022',
            backgroundColor: '#e7e7ee7',
            data: [60, 79, 90, 70],
          },
          {
            label: '2023',
            backgroundColor: '#478058',
            data: [50, 100, 75, 65],
          },
        ],
      },
      chartOptions: {
        responsive: true,
      },
    };
  },
};
</script>
