<template>
  <Bar id="my-chart-id" :options="chartOptions" :data="chartData" />
</template>

<script>
// eslint-disable-next-line import/no-extraneous-dependencies
import { Bar } from 'vue-chartjs';
// eslint-disable-next-line import/no-extraneous-dependencies
import {
  Chart as ChartJS,
  Title,
  Tooltip,
  Legend,
  BarElement,
  CategoryScale,
  LinearScale,
} from 'chart.js';

ChartJS.register(Title, Tooltip, Legend, BarElement, CategoryScale, LinearScale);

export default {
  name: 'BarChart',
  components: { Bar },
  data() {
    return {
      chartData: {
        labels: [
          'Q1',
          'Q2',
          'Q3',
          'Q4',
        ],
        datasets: [{ label: '', backgroundColor: '#888888', data: [142, 179, 179, 123] }],
      },
      chartOptions: {
        responsive: true,
      },
    };
  },
};
</script>
